<template>
  <div class="panel columns">
    <div class="column">
      <div class="form-group">
        <label for="first-line">First Line</label>
        <textarea
          id="first-line"
          class="form-control"
          v-model="form.firstLine">
        </textarea>
      </div>
      <div class="form-group">
        <label for="second-line">Second Line</label>
        <textarea
          id="second-line"
          class="form-control"
          v-model="form.secondLine">
        </textarea>
      </div>
    </div>
    <div class="column">
      <div class="form-group">
        <label for="third-line">Third Line</label>
        <textarea
          id="third-line"
          class="form-control"
          v-model="form.thirdLine">
        </textarea>
      </div>
      <div class="form-group">
        <label for="episode">"EPISODE"</label>
        <textarea
          id="episode"
          class="form-control"
          v-model="form.episode">
        </textarea>
      </div>
    </div>
    <div class="column">
      <div class="form-group">
        <label for="title-line">Title Line</label>
        <textarea
          id="title-line"
          class="form-control"
          v-model="form.titleLine">
        </textarea>
      </div>
      <div class="form-group">
        <label for="title-font">Title Font</label>
        <textarea
          id="title-font"
          class="form-control"
          v-model="form.titleFont">
        </textarea>
      </div>
    </div>
    <div class="column download">
      <div class="form-group">
        <label for="">Download</label>
        <button class="download-button" @click="download">Save as PNG</button>
      </div>
    </div>
  </div>
</template>

<script>
  // Dependencies
  import download from 'downloadjs'

  export default {
    name: 'panel',
    props: {
      form: {
        required: true,
        type: Object
      }
    },
    watch: {
      form: {
        handler(newValue) {
          this.$emit('update:foo', newValue)
        },
        deep: true
      }
    },
    methods: {
      download() {
        const canvas = document.getElementById('canvas')
        canvas.toBlob(blob => {
          download(blob, 'evangelion-card.png', 'image/png')
        })
      }
    }
  }
</script>

<style lang="stylus" scoped>
  .panel
    color: white
    max-width: 960px
    padding-top: 10px
    .form-group
      padding: 10px
    .form-control
      width: 100%
      background-color: transparent
      border: 1px solid white
      color: white
      min-height: 40px
    label
      display: block
      margin-bottom: 10px
      user-select: none

  .download
    display: flex

  .download-button
    cursor: pointer
    height: 40px
    background-color: transparent
    color: white
    border: 1px solid
    padding: 0 20px
    font-size: 16px
    &:hover
      background-color: rgba(47, 47, 47, 0.72)

  @media screen and (min-width: 768px)
    .panel
      padding-left: 30px
</style>



